    $(document).ready(function () {
    //   
    $.validator.addMethod(
      "letters",
      function (value, element) {
        return this.optional(element) || /^[a-zA-Z ]*$/.test(value);
      },
      "Please enter only letters."
    );
  
    //     
    $.validator.addMethod(
      "phoneCZ",
      function (value, element) {
        return this.optional(element) || /^(\+420)?\d{9}$/.test(value);
      },
      "Prosm, zadejte platn telefonn slo ve formtu +420XXXXXXXXX nebo XXXXXXXXX."
    );
  
    //    
    var $form = $(".formLogin"),
      $successMsg = $(".alert");
    $form.validate({
      rules: {
        email: {
          required: true,
          email: true,
        },
        password: {
          required: true,
          minlength: 6,
          maxlength: 70,
        },
      },
      messages: {
        email: "Prosm, uvete platnou e-mailovou adresu",
        password: "Uvete heslo",
      },
      submitHandler: function () {
        $successMsg.show();
      },
    });
  
    //    
    var $formRegistration = $(".formRegistration");
    $formRegistration.validate({
      rules: {
        titul: {
          letters: true,
        },
        name: {
          required: true,
          minlength: 3,
          letters: true,
        },
        surname: {
          required: true,
          minlength: 3,
          letters: true,
        },
        emailPerson: {
          required: true,
          email: true,
        },
        telNumber: {
          phoneCZ: true,
          required: true,
        },
        passPerson: {
          required: true,
          minlength: 6,
          maxlength: 70,
        },
        passPersonn: {
          required: true,
          equalTo: "#passPerson",
        },
        street: {
          required: true,
          minlength: 4,
        },
        refNumber: {
          required: true,
        },
        refNumComp: {
          required: true,
        },
        psc: {
          required: true,
          minlength: 4,
        },
        city: {
          required: true,
        },
        state: {
          required: true,
        },
        streetComp: {
          required: true,
          minlength: 4,
        },
        pscComp: {
          required: true,
          minlength: 4,
        },
        cityComp: {
          required: true,
        },
        nameComp: {
          required: true,
          minlength: 3,
          letters: true,
        },
        dic: {
          required: true,
          minlength: 4,
        },
        ico: {
          required: true,
          minlength: 4,
        },
      },
      messages: {
        name: "Prosm, uvete svoje jmno",
        surname: "Prosm, uvete svoje pjmen",
        emailPerson: "Prosm, uvete platnou e-mailovou adresu",
        telNumber:
          "Prosm, zadejte platn telefonn slo ve formtu +420XXXXXXXXX",
        passPerson: "Prosm, uvete heslo",
        passPersonn: "Prosm, znovu uvete heslo",
        street: "Prosm, uvete ulici",
        refNumber: "Prosm, uvete slo popisn",
        psc: "Prosm, uvete PS",
        city: "Prosm, uvete msto",
        state: "Prosm, uvete zemi",
        streetComp: "Prosm, uvete ulici",
        pscComp: "Prosm, uvete PS",
        cityComp: "Prosm, uvete msto",
        nameComp: "Prosm, uvete nzev spolenosti",
        ico: "Prosm, uvete IO",
        dic: "Prosm, uvete DI",
        refNumComp: "Prosm, uvete slo popisn",
      },
      submitHandler: function () {
        $successMsg.show();
      },
    });
  
    //   / 
    $(".toggle-password").click(function () {
      var input = $(this).siblings($(this).attr("toggle"));
      if (input.attr("type") === "password") {
        input.attr("type", "text");
        $(this).addClass("active");
      } else {
        input.attr("type", "password");
        $(this).removeClass("active");
      }
    });
  
    var companyCheckbox = document.getElementById("companyCheckbox");
    if (companyCheckbox) {
      companyCheckbox.addEventListener("change", function () {
        var companyFields = document.querySelectorAll(".company-fields");
        companyFields.forEach(function (field) {
          field.style.display = this.checked ? "block" : "none";
        }, this);
      });
    }
  });
  
